.main_section{
	height: 100%;
}

.main_container{
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	padding: 20px 10px;
	gap: 16px;

}

.loading{
	display: flex;
	align-items: center;
	height: 100vh;
	margin: auto;
}

@media (max-width:680px){
	.main_container{
		padding: 16px 0;
	}
}